.current {
    color: maroon;
}

.button {
  padding: 10px;
  background-color: #D3D3D3; /* Light grey color */
  color: rgb(32, 31, 31);
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;

  &:hover {
    color: white;
    background-color: #800000; /* Maroon color on hover */
  }
}

.button-container {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

/* Container for centering */
.container {
  max-width: 900px;
  margin: auto;
  padding: 20px;
  text-align: left;
}

/* Global styles */
body {
  font-family: 'Arial', sans-serif;
  color: #333333;
  background-color: #f9f9f9;
  line-height: 1.6;
}

